@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");
/*
  0 - 600px:       Phone
  600 - 900px:     Tablet portrait
  900 - 1200px:    Tablet landscape
  [1200 - 1800] is where our normal styles apply. > Desktop frst aprouch
  1800px + :       Big desktop
*/
/*
 Example:

h1 {
  font-size: rem(33.536);
}

// In CSS:
h1 {
  font-size: 3.3536rem;
}

*/
.cta-btn, .cta-form__btn {
  font-size: 1.5rem;
  text-decoration: none;
  font-family: inherit;
  border: none;
  cursor: pointer;
  border-radius: 1.2rem;
  color: var(--white);
}

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}
@media (max-width: ) {
  html {
    font-size: 77%;
  }
}
@media (min-width: 112.5em) {
  html {
    font-size: 77%;
  }
}
@media (max-width: 56.25em) {
  html {
    font-size: 50%;
  }
}
@media (max-width: 37.5em) {
  html {
    font-size: 62.5%;
  }
}

body {
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --primary: #FF7979;
  --primary-2: #38CC8B;
  --secondary: #5E54A4;
  --input-text-color: #3D3B48;
  --gray: #BAB7D4;
  --white: #fff;
  --success-color: #38CC8B;
  --error-color: #FF7979;
}

.section-cta {
  height: 100vh;
  padding: 0 11.46%;
  font-size: 1.6rem;
  line-height: 2.6rem;
  color: var(--white);
  background-color: var(--primary);
  background-image: url(/images/bg-intro-desktop.png);
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 37.5em) {
  .section-cta {
    flex-direction: column;
    min-height: 100rem;
  }
}

.cta-container {
  display: grid;
  grid-template-columns: repeat(2, 0.5fr);
  column-gap: 4.5rem;
  background-color: transparent;
}
@media (max-width: 75em) {
  .cta-container {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 37.5em) {
  .cta-container {
    grid-template-columns: 1fr;
    column-gap: 0rem;
  }
}

.cta-text {
  align-self: center;
  justify-self: center;
}
@media (max-width: 37.5em) {
  .cta-text {
    text-align: center;
  }
}
.cta-text__heading {
  font-size: 5rem;
  line-height: 5.5rem;
  font-weight: 700;
  margin-bottom: 2.4rem;
}
@media (max-width: 37.5em) {
  .cta-text__heading {
    font-size: 2.8rem;
    line-height: 3.6rem;
    margin-bottom: 1.6rem;
  }
}
.cta-text__description {
  font-weight: 500;
}
@media (max-width: 37.5em) {
  .cta-text__description {
    text-align: center;
    margin: 0 2rem 6.4rem;
  }
}

.cta {
  border-radius: 1.2rem;
  box-shadow: 0px 8px 0px rgba(0, 0, 0, 0.14688);
  display: flex;
  flex-direction: column;
  gap: 2.4rem;
}
.cta-btn {
  width: 100%;
  padding: 1.7rem 11.1rem;
  background-color: var(--secondary);
  box-shadow: 0px 8px 0px rgba(0, 0, 0, 0.14688);
}
@media (max-width: 37.5em) {
  .cta-btn {
    padding: 1.8rem 6.7rem;
  }
}
.cta-btn span {
  font-weight: 500;
}
.cta-form {
  padding: 4rem;
  background-color: var(--white);
  border-radius: 1.2rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  font-family: inherit;
}
@media (max-width: 37.5em) {
  .cta-form {
    padding: 2.4rem;
    gap: 1.6rem;
  }
}
.cta-form__input {
  padding: 1.5rem 3.2rem;
  border: 1px solid var(--gray);
  border-radius: 0.5rem;
  font-size: 1.4rem;
  font-weight: 500;
  font-family: inherit;
  color: var(--input-text-color);
}
@media (max-width: 37.5em) {
  .cta-form__input {
    padding: 1.5rem 1.941rem;
  }
}
.cta-form__input::placeholder {
  font-size: 1.4rem;
  font-weight: 500;
  color: var(--input-text-color);
}
.cta-form__btn {
  padding: 1.5rem 1.34rem;
  background-color: var(--primary-2);
  font-size: 1.5rem;
  text-transform: uppercase;
  letter-spacing: 0.1rem;
  line-height: 2.6rem;
  font-weight: 600;
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.0908818);
}
.cta-form__terms {
  color: var(--gray);
  font-size: 1.1rem;
  text-align: center;
  margin-top: -1.2rem;
  align-self: center !important;
}
@media (max-width: 37.5em) {
  .cta-form__terms {
    margin: 0 4rem;
  }
}
.cta-form__terms span {
  color: var(--primary);
  font-weight: 700;
}

*:focus {
  outline: none;
  box-shadow: 0 0 0 0.3rem rgba(255, 121, 121, 0.5);
}

.cta-form__input.success {
  border-color: var(--success-color);
}

.cta-form__input.error {
  border-color: var(--error-color);
  background-image: url(/images/icon-error.svg);
  background-repeat: no-repeat;
  background-position: 95%;
}

.cta-form small:not(:last-child) {
  margin-top: -2rem;
  align-self: flex-end;
  display: none;
  color: var(--error-color);
}